/* 
 * Base & variables
 */
@import url("https://fonts.googleapis.com/css2?family=PT+Sans&family=Work+Sans:wght@200;300;400;500&display=swap");
* {
  box-sizing: border-box;
  font-family: "Work Sans", sans-serif;
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
p,
blockquote,
table,
th,
td,
embed,
object {
  padding: 0;
  margin: 0;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  background: #f4f5fc;
  color: #111111;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
h1,
h2,
h3,
h4,
h5,
h6,
strong,
th,
var {
  font-weight: normal;
  font-style: normal;
}

ul {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1em;
}

q:before,
q:after {
  content: "";
}

a,
ins {
  text-decoration: none;
}

/* 
 * Container
 */
.container {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  margin: 0 auto;
  padding: 0 10px;
  align-items: center;
  position: relative;
  max-width: 800px;
}

/* 
 * Components
 */
.title {
  font-size: 49px;
  position: absolute;
  bottom: 79px;
  right: -54px;
  color: #fff;
  transform: rotate(-90deg);
  letter-spacing: 0.5px;
  font-family: "PT Sans";
  width: 180px;
}
.title-wrapper {
  position: relative;
  width: 137px;
  background: #5a61ed;
}

.alert {
  color: #5f2120;
  background: #fddfdf;
  position: absolute;
  width: 100%;
  top: 5%;
  padding: 14px 20px;
  padding-left: 40px;
  z-index: 99;
  border-radius: 4px;
  opacity: 0;
}
.alert-icon {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  fill: #ef5350;
}
.alert-title {
  font-weight: 500;
  margin-bottom: 6px;
  font-size: 18px;
}
.alert-message {
  font-weight: 300;
  letter-spacing: 0.1px;
}

.login-text {
  font-weight: 300;
}
.login-link {
  font-weight: 500;
  text-decoration: underline;
}

.form {
  max-width: 485px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin: 38px 22px 33px 21px;
  min-height: 409px;
}
.form-wrapper {
  display: flex;
  background: #fff;
  width: 100%;
  max-width: 665px;
  position: relative;
  overflow: hidden;
  z-index: 0;
}
.form-title {
  font-size: 22px;
  font-weight: 500;
  letter-spacing: -0.46px;
}
.form-subtitle {
  font-size: 14px;
  font-weight: 300;
  margin-bottom: 20px;
}
.form-inner {
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  justify-content: space-between;
}
.form-item {
  min-width: 235.5px;
  display: flex;
  flex-direction: column;
  margin-bottom: 3px;
  position: relative;
}
.form-item.valid::after {
  width: 8px;
  height: 10px;
  background: #fff url("../assets/check.svg");
  position: absolute;
  right: 0px;
  bottom: 50%;
  transform: translateY(100%);
  content: "";
  display: block;
  transition: 0.2s ease-in;
}
.form-label {
  color: #7c7c7c;
  font-size: 12px;
  font-weight: 300;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.form-input {
  border: 0;
  border-bottom: 1px solid #f2f2f2;
  height: 27px;
  outline: transparent;
  font-size: 14px;
  background: transparent;
}
.form-bottom {
  margin-top: 95px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
}
.form-button {
  background: #5a61ed;
  color: #fff;
  border: none;
  padding: 7px 26px;
  font-size: 14px;
  font-family: "PT Sans";
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.form-image {
  position: absolute;
  bottom: -55px;
  right: -100px;
  width: 410px;
  height: 410px;
  z-index: -1;
}

.select {
  position: relative;
}
.select-placeholder {
  font-size: 14px;
  position: relative;
  border-bottom: 1px solid #f2f2f2;
  height: 27px;
  cursor: pointer;
  padding-right: 30px;
  display: flex;
  align-items: center;
  min-width: 50px;
}
.select-placeholder.open::after {
  transform: translateY(50%) rotate(180deg);
}
.select-placeholder::after {
  width: 8px;
  height: 5px;
  background: url("../assets/shevron-down.svg") no-repeat;
  position: absolute;
  right: 0px;
  bottom: 50%;
  transform: translateY(50%);
  content: "";
  display: block;
  transition: 0.2s ease-in;
}
.select-list {
  position: absolute;
  top: 100%;
  left: 0;
  display: flex;
  flex-direction: column;
  min-width: 100%;
  background: #fff;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 999;
  margin: 0;
  padding: 0;
  scrollbar-width: thin;
  overscroll-behavior: contain;
  text-align: center;
  max-height: 140px;
}
.select-list::-webkit-scrollbar {
  width: 5px;
  padding: 5px;
}
.select-list::-webkit-scrollbar-thumb {
  background-color: #7c7c7c;
}
.select-group {
  display: flex;
  position: relative;
  justify-content: space-between;
}
.select-item {
  cursor: pointer;
  padding: 6px 12px;
  font-size: 14px;
}
.select-item:hover {
  background-color: #f2f2f2;
}

.radio-group {
  display: flex;
  align-items: center;
  height: 27px;
}
.radio-group label {
  font-size: 14px;
  color: #111111;
  font-weight: 400;
  margin-right: 20px;
}

input[type=radio]:checked,
input[type=radio]:not(:checked) {
  position: absolute;
  left: -9999px;
}

input[type=radio]:checked + label:before,
input[type=radio]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  border: 1px solid #5a61ed;
  background-color: #ffffff;
}

input[type=radio]:checked + label:before,
input[type=radio]:not(:checked) + label:before {
  border-radius: 100%;
}

input[type=radio]:checked + label:after,
input[type=radio]:not(:checked) + label:after {
  content: "";
  position: absolute;
  left: 3px;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  border-radius: 100%;
  background-color: #5a61ed;
}

input[type=radio]:checked + label:after {
  opacity: 1;
}

input[type=radio]:not(:checked) + label:after {
  opacity: 0;
}

input[type=checkbox]:checked + label,
input[type=checkbox]:not(:checked) + label,
input[type=radio]:checked + label,
input[type=radio]:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 19px;
  cursor: pointer;
}

input[type=password]::-ms-reveal,
input[type=password]::-ms-clear {
  display: none;
}

input[type=password] {
  font-size: 35px;
  height: 19px;
  margin-top: 8px;
  padding-left: 3px;
  margin-left: -1px;
  max-width: 235.5px;
}

.password-mask {
  position: absolute;
  font-size: 30px;
  font-family: "PT Sans";
  letter-spacing: -3px;
  bottom: 4px;
  margin-left: -2px;
  height: 18px;
  line-height: 15px;
  max-width: 100%;
  background: #fff;
  z-index: 1;
  padding-right: 1px;
  overflow: hidden;
}

.error label,
.error input,
.error span,
.error div {
  color: #ff2222;
}
.error input,
.error .select-placeholder {
  border-bottom: 2px solid #ff2222 !important;
}

/* 
 * Utilities
 */
.bg_0 {
  stroke-dasharray: 1201 1203;
  stroke-dashoffset: 1202;
  animation: bg_draw 2000ms linear 0ms forwards;
}

.bg_1 {
  stroke-dasharray: 723 725;
  stroke-dashoffset: 724;
  animation: bg_draw 1000ms linear 144ms forwards;
}

.bg_2 {
  stroke-dasharray: 271 273;
  stroke-dashoffset: 272;
  animation: bg_draw 1000ms linear 288ms forwards;
}

.bg_3 {
  stroke-dasharray: 1295 1297;
  stroke-dashoffset: 1296;
  animation: bg_draw 1000ms linear 433ms forwards;
}

.fade-1 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 0s forwards;
}

.fade-2 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 0.3s forwards;
}

.fade-3 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 0.6s forwards;
}

.fade-4 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 0.9s forwards;
}

.fade-5 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 1.2s forwards;
}

.fade-6 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 1.5s forwards;
}

.fade-7 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 1.8s forwards;
}

.fade-8 {
  opacity: 0;
  pointer-events: none;
  animation: fade 0.5s ease 2.1s forwards;
}

.button-error {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

.alert-show {
  animation: alert 4s cubic-bezier(0.36, 0.07, 0.19, 0.97);
}

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
@keyframes alert {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes bg_draw {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes bg_fade {
  0% {
    stroke-opacity: 1;
  }
  90% {
    stroke-opacity: 1;
  }
  100% {
    stroke-opacity: 0;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.hidden {
  display: none;
}

@media screen and (max-width: 660px) {
  .alert {
    border-radius: 0;
  }
  .title {
    transform: rotate(0deg) translate(0%, -50%);
    top: 50%;
    left: 10%;
    bottom: 0;
    right: 0;
  }
  .title-wrapper {
    display: flex;
    width: 100%;
    height: 100px;
  }
  .form {
    margin: 30px auto;
    padding: 0 15px;
    max-width: 100%;
  }
  .form-wrapper {
    flex-direction: column;
  }
  .form-item {
    width: 100%;
  }
  .form-image {
    right: 50%;
    transform: translateX(50%);
    bottom: 0;
    width: 90%;
    height: auto;
  }
  .form-bottom {
    margin-top: 60px;
  }
  .select-group {
    justify-content: start;
  }
  .select-group .select {
    margin-right: 15px;
  }
  .select-list {
    max-height: 230px;
  }
  input[type=password] {
    max-width: 100%;
  }
}
@media screen and (max-width: 440px) {
  .container {
    padding: 0;
    height: auto;
  }
}
@media screen and (max-height: 480px) {
  .alert {
    position: fixed;
  }
  .container {
    height: auto;
    padding: 0;
  }
}
@media screen and (max-height: 760px) and (max-width: 660px) {
  .alert {
    position: fixed;
  }
  .container {
    height: auto;
    padding: 0;
  }
}
@-moz-document url-prefix() {
  input[type=password] {
    font-size: 25px;
  }
  .password-mask {
    bottom: 2px;
    margin-left: 0px;
  }
}